<template>
  <div class="spinner"></div>
</template>


<style>
.spinner{
    width: 40px;
    height: 40px;
    margin: 40px auto;
    border-radius:50% ;
    border: 4px solid transparent;
    border-top-color: #ff8800;
    animation: spin 1s infinite ease-in;
}
@keyframes spin{
    to{
        transform: rotateZ(360deg);
    }
}
</style>